/* write your css here */

*{
    margin:0px;
    padding:0px;
    box-sizing: border-box;
}

body{
    height:100vh;
    font-family: 'Courier New', Courier, monospace;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title{
    font-size: 3rem;
    margin:6vh;
    border:7px solid rgb(1,17,160);
    border-radius: 20px;
    padding:1vh 5vw;
}

.cards{
    display: flex;
    flex-wrap: wrap;
    max-width: 1300px;
    margin:0 auto;
}

.card{
    width:400px;
    height:max-content;
    margin:1rem;
    padding:1rem;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    align-items: center;
    box-shadow: -1px 1px 5px 1px orange
}

.image{
    width:380px;
    aspect-ratio: 1/1;
    object-fit: cover;
}

.tour-info{
    margin:20px 5px;
}

.tour-price{
    color:#1faa59;
    font-size: 1.3rem;
}

.tour-name{
    font-size:1.5rem;
}

.read-more{
    color:#12b0e8;
    cursor: pointer;
}

.btn-red{
    margin-top:18px;
    padding:10px;
    border:1px solid #b4161b;
    cursor: pointer;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
}

.btn-red:hover{
    background-color: red;
    color:white;
    transition: all 0.2s;
}

.btn-add{
    margin-top: 18px;
        padding: 10px;
        border: 1px solid #b4161b;
        cursor: pointer;
        border-radius: 10px;
        font-size: 18px;
        font-weight: bold;
}

.btn-add:hover{
        background-color: red;
        color: white;
        transition: all 0.2s;
}

.refresh{
    height:100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.btn-white{
    margin-top:18px;
    padding:10px 80px;
    border:1px solid white;
    cursor: pointer;    
}

.btn-white:hover{
    background-color: white;
    color:black;
    transition: all 0.2s;
}